import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Layout from '../components/Layout.vue'
import LayoutPractice   from '../components/LayoutPractice.vue'
import MockTable from '../components/MockJS/MockTable.vue'


Vue.use(VueRouter)

const routes = [
  { path: '/',name: 'index',component: ()=>import('../views/index.vue') },
  // 前端技术demo 页
  {
    path: '/home',
    name: 'Home',
    component: Home,
    redirect:'/welcome',
    children:[
         {path:'/welcome',component:()=>import('../views/Welcome.vue')},
         {path:'/mocktable',component:MockTable},
         { path:'/layoutpractice',component:LayoutPractice,
           children:[
            { path:'addblog',component:()=>import('../components/LayoutPractice/AddBlog.vue')},
             {path:'showview',component:()=>import('../components/LayoutPractice/ShowView.vue')},
           ]
         },
         { path:'/shoppingcart',component:()=>import('../views/ShoppingCart') },
         { path:'/carousel',component:()=>import('../views/Carousel') }
    ]
  },
  { path:'/layout', component:Layout },
  { path:'/resume',component:()=>import('../views/Resume.vue') },

  {
    path:'/routerdemo',component:()=>import('../views/RouterDemo.vue'),
    children:[
      { path:'home',component:()=>import('../components/RouterDemo/Home.vue')},
      { path:'all',component:()=>import('../components/RouterDemo/VideoList.vue')},
      // { path:'weekly',component:()=>import('../components/RouterDemo/Weekly.vue')},
      { path:'weekly',
        components:{
          default:()=>import('../components/RouterDemo/VideoList.vue'),
          weekly:()=>import('../components/RouterDemo/VideoHeader.vue')
        }
      },
      { path:'history',component:()=>import('../components/RouterDemo/VideoList.vue')},
      { path:'rank',component:()=>import('../components/RouterDemo/Rank.vue'),redirect:'rank/douga',
        children:[
           {path:'douga',component:()=>import('../components/RouterDemo/rank/Douga')},
           {path:'dance',component:()=>import('../components/RouterDemo/rank/Dance')},
           {path:'music',component:()=>import('../components/RouterDemo/rank/Music')},
        ]
      },
      {    name:'video', path: '/video/:id',component: ()=>import('../components/Video.vue')},
    ]


  },

  { path:'/loginpage',component:()=>import('../views/LoginPage.vue')},
  { path:'/fontpage',component:()=>import('../views/FontPage.vue')},
  { path:'/vchart',component:()=>import('../views/charts/VChart.vue') },
  {
    path:'/shoppingct',
    component:()=>import('../views/shoppingcart/shoppingct.vue'),
    redirect:'shoppingct/list',
    children:[
      {
        path:'list',
        meta:{
          title:'购物车'
        },
        component:()=>import('../views/shoppingcart/list.vue')
      },
      {
        path:'cart',
        component:()=>import('../views/shoppingcart/cart.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes,
  linkActiveClass:'ac',
  linkExactActiveClass:'active'
})


router.beforeEach((to, from, next) => {
  if(to.meta.title){
    window.document.title=to.meta.title;
  }
  if (to.path === '/index')
    return next();
  next();
})


export default router
