<template>
  <div class="showView">
    <BlogItem v-for="item in listArr" :key="item.id"   :itemObj="item"/>
    <div class="pageGroup">
      <div class="btn btnLeft" @click="btnLeft">&lt;上一页</div>
      <div class="btn btnRit" @click="btnRight">下一页 &gt;</div>
    </div>
    <div class="toast" v-show="showToast">
      数据加载中：
    </div>
  </div>
</template>

<script>
import BlogItem from './BlogItem.vue'
import axios from 'axios'
export default{
  data(){
    return{
      listArr:[
        {myTitle:"标题一",myContent:"1111111111111111111111111111111111"},
        {myTitle:"标题二",myContent:"2222222222222222222222222222222222222"},
        {myTitle:"标题三",myContent:"3333333333333333333333333333333333333333333333333333"}
      ],
      page:1,
      showToast:false
    }
  },
  components:{
    BlogItem
  },
  created() {
     this.getData();
  },
  methods:{
    btnLeft(){
      if(this.page<=1){
        this.$message.warning('已经到第一页来！');
        return ;
      }
      this.showToast=true;
      this.page--;
      this.getData();
      this.scrTop();
    },
    btnRight(){
      if(this.listArr.length<9){
        this.$message.warning('已经到最后一页来！');
        return ;
      }
      this.showToast=true;
      this.page++;
      setTimeout(()=>{
          this.getData();
      },100);
      this.scrTop();
    },
    getData(){
      axios({
        url:"http://ku.qingnian8.com/dataApi/blog/showBlog.php",
        params:{
          page:this.page
        }
      }).then(res=>{
        console.log(res);
        this.listArr=res.data;
        this.showToast=false;
      })
    },

    //返回顶部的函数
    scrTop(){
      if(document.body.scrollTop){
        document.body.scrollTop=0;
      }else{
        document.documentElement.scrollTop=0;
      }

    }



  }

}
</script>

<style lang="less" scoped>
.showView{
  padding: 0 30px;
}
.pageGroup{
   padding: 40px;
   display: flex; justify-content: center; align-items: center;
   .btn{
     color: #37A982; border: 1px solid #37A982;
     padding: 10px 20px;  margin: 0 10px;   font-size: 18px;
     cursor: pointer;  transition: 0.3s;
     &:hover{
       color: #fff; background: #37A982;
     }
   }
}
.toast{
  position: fixed; left: 0;  top: 0;
  width: 100%;   height: 100%;
  background: rgba(0,0,0,0.3);   color: #fff;
  display: flex;  font-size: 30px;
   justify-content: center; align-items: center;
}
</style>
