<template>
  <div id="container">
    <el-row :gutter="16">
       <el-col  :span="4">
         <el-input
           placeholder="请输入标题"
           clearable>
         </el-input>
       </el-col>
       <el-col :span="4">
         <el-input
           placeholder="请输入内容"
           clearable>
         </el-input>
       </el-col>
       <el-button type="primary" >添加</el-button>
     </el-row>


     <el-table
       :data="tableData" border    stripe
       style="width: 100%;">
       <el-table-column    label="图片"     width="150">
         <template slot-scope="scope">
            <img :src="scope.row.image" alt="" >
         </template>
       </el-table-column>
       <el-table-column   prop="title"      label="标题"         width="180">
       </el-table-column>
       <el-table-column   prop="content"      label="内容"       width="360">
       </el-table-column>
       <el-table-column    prop="add_time"       label="添加时间">
       </el-table-column>
       <el-table-column   label="操作">
         <template slot-scope="scope">
            <el-button type="danger">删除</el-button>
         </template>
       </el-table-column>
     </el-table>


   <el-pagination
       :current-page="queryInfo.pagenum"    
       @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
       :page-size="queryInfo.pagesize"   :page-sizes="[5, 10, 15, 30]"
       layout="total, sizes, prev, pager, next, jumper"
       :total="total">
   </el-pagination>
  </div>
</template>


<script>
export default {
  data(){
    return{
      isCollapse:true,
      tableData: [],
      // 查询条件
      queryInfo:{
        pagenum:1,
        pagesize:5
      },
      total:0,
    }
  },
  created() {
    this.getTableData();
  },
  methods:{
    async getTableData(){
      // axios.get('/api/get/news',{
      //   params:{
      //     pagenum:1,
      //     pagesize:10
      //   }
      // }).then(res=>{
      //   console.log(res);
      //   this.tableData=res.list;
      // })
      const { data:res } =await this.$http.get('/api/get/news',{
        params:{
          pagenum:this.queryInfo.pagenum,
          pagesize:this.queryInfo.pagesize
        }
      });
      if(res.status!=200)
          console.log("失败")
      this.tableData=res.list;
      this.total=res.total;
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum=newPage;
      console.log(this.queryInfo.pagenum);
      this.getTableData();
      console.log(`当前页: ${this.queryInfo.pagenum}`);
    },
    handleSizeChange(newPageSize) {
      this.queryInfo.pagesize=newPageSize;
      this.getTableData();
    }


  }
}
</script>

<style lang="less" scoped>
#container{
   // overflow:scroll;
}
.el-table{
  margin-top: 6px;
}

</style>
