<template>
  <div class="itembox">
    <div class="top">
      <h2>{{itemObj.title}}</h2>
      <div class="time">{{getMyDate(itemObj.posttime)}}</div>
    </div>
    <div class="center">
      {{itemObj.content}}
    </div>
    <div class="info">
      <div class="list">
        所属分类：
        <span>{{itemObj.classify}}</span>
      </div>
      <div class="author">
        作者：
        <span>{{itemObj.author}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import common from './js/common.js'
// console.log(common.getStrLen('123456789',6));
// console.log(common.myDate('1621854299'));

export default{
props:{
  // 如果传递子元素的是一个对象，那么接收默认值必须是function。函数是一次声明，多次调用。这个对象可以在多个对象使用，所以用一个函数接收更方便。数组也一样。
  itemObj:{
    type:Object,
    default:function(){
      return {}
    }
  }
},
methods:{
  getMyDate(time){
    return common.myDate(time,3);
  }
}
}
</script>

<style lang="less" scoped>
.itembox{
  padding: 30px 0;
  .top{
    display:flex; justify-content: space-between;   align-items: center;
    padding-right: 10px;
    h2{
      font-size: 25px; color:#333;  width: 750px;
    }
    .time{
      font-size: 14px;
      color: 888;
    }
  }
  .center{
    background: #F6F6F6; border-radius: 5px;
    padding: 15px 10px;  font-size: 18px;
    line-height: 1.8em;   color: #444;   margin-bottom: 12px;
  }
  .info{
    display: flex; justify-content: space-between;
    font-size: 14px; color:#888;
    span{
      font-size: 16px; color:#666;
    }
  }
}
</style>
